body {
  background-color: #f5f5f5;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.search-form {
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.search-form input[type="text"] {
  border: none;
  border-radius: 4px;
  margin-right: 10px;
  padding: 10px;
  font-size: 16px;
}

.search-form button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
}

.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  display: grid;
}

.photo-card {
  background-color: #fff;
  position: relative;
  overflow: hidden;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .1);
}

.photo-card .photo-link {
  display: block;
}

.photo-card .photo {
  width: 100%;
  height: auto;
  display: block;
}

.photo-card .info {
  background-color: rgba(255, 255, 255, .8);
  padding: 10px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.photo-card .info-item {
  margin-right: 10px;
  font-size: 14px;
  display: inline-block;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  margin: 0 auto;
  padding: 10px 20px;
  font-size: 16px;
  transition: all .2s ease-in-out;
  display: block;
}

.hide {
  display: none;
}

